@import url("https://fonts.google.com/specimen/Press+Start+2P");
@import url("https://fonts.google.com/specimen/VT323");




.App {
  text-align: center;
  font-family: "Press Start 2P", "VT323";
  color: blue;
}

*{
  box-sizing: border-box;
}

body {
  overflow: hidden;
}

.overlay {
  opacity: 0.85;
  width: 100%;
  height: 100%;
  z-index: 10;
  top: 0;
  left: 0;
  position: fixed;
}

.moving-background{
  z-index: -1;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-image: url("./img/background.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-position: 40% 40%;
}